.card-portfolio .image-container {
  width: 100%;
  border-radius: 2rem;
  aspect-ratio: 4/3;
  overflow: hidden;
  filter: saturate(0) sepia(0.1) brightness(0.9);
  transition: all ease-in-out 0.15s;
}
