  width: 100%;
  object-fit: cover;
  object-position: center;
  transform-origin: center center;
  transition: transform ease-in-out 0.2s;
}
.card-portfolio:hover .image-container {
  filter: saturate(1);
}
.card-portfolio:hover .image-container img {
  transform: scale(1.1);
}

