@keyframes noise {
  0% {
    background-position: 0% 0%;
  }
  10% {
    background-position: -5% -10%;
  }
  20% {
    background-position: -15% -5%;
  }
  30% {
    background-position: -7% -25%;
  }
  40% {
    background-position: -20% -25%;
  }
  50% {
    background-position: -25% 10%;
  }
  60% {
    background-position: -15% -5%;
  }
  70% {
    background-position: 0% 15%;
  }
  80% {
    background-position: 25% 35%;
  }
  90% {
    background-position: -10% 10%;
  }
  100% {
    background-position: 0% 0%;
  }
}
@keyframes bg-circle-1 {
  0% {
    transform: scale(2, 2);
  }
  25%, 50% {
    transform: scale(1, 1);
  }
  75%, 100% {
    transform: scale(2, 2);
  }
}
@keyframes bg-circle-2 {
  0% {
    transform: scale(1, 1);
  }
  25%, 50%, 75% {
    transform: scale(2, 2);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes bg-circle-3 {
  0% {
    transform: scale(1, 1);
    opacity: 0;
  }
  25%, 50%, 75% {
    transform: scale(2, 2);
    opacity: 1;
  }
  100% {
    transform: scale(1, 1);
    opacity: 0;
  }
}
@keyframes bg-circle-4 {
  0%, 25% {
    transform: scale(2, 2);
  }
  50%, 75% {
    transform: scale(1, 1);
  }
  100% {
    transform: scale(2, 2);
  }
}
@keyframes bg-circle-5 {
  0% {
    transform: scale(2, 2);
    opacity: 0.8;
  }
  25%, 50% {
    transform: scale(1, 1);
    opacity: 0;
  }
  75%, 100% {
    transform: scale(2, 2);
    opacity: 0.8;
  }
}
@keyframes bg-circle-6 {
  0%, 25% {
    transform: scale(1, 1);
    opacity: 1;
  }
  50%, 75% {
